
@import url('https://fonts.googleapis.com/css?family=Dosis');
.dropdown {
  padding-left:60px;
  padding-right: 60px;
}
.nav-link {
  padding-left: 50px;
}
/*-------Pengkar--------------*/
.dropdown-content-pengkar {
  display: none;
  position: absolute;
  background-color: #b4311e;
  padding-top: 10px;
  min-width: 310px;
  -webkit-box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
          box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}
.dropdown-content-pengkar a {
  color: #f9f9f9;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}
.dropdown-content-pengkar a:hover {background-color: #992b1a
}
.dropdown:hover .dropdown-content-pengkar {
  display: block;
}
.dropdown:hover .drop-pengkar {
  background-color: #b4311e;
}

