.row {
  margin: 0px;
}
nav.navbar {
  padding-top: 0px;
  padding-bottom: 0px;
}
nav .navbar-nav {
  margin-left: 30px;
}
nav .navbar-nav>.nav-item>.nav-link {
  padding-left: 30px;
  padding-right: 30px;
}
#app {
  overflow: hidden;
}
#title {
  background-color: #FFD05C;
  padding: 75px 20px 75px 20px;
}
.login-info {
  position: fixed;
  background-color: #B4311E;
  opacity: 0.8;
  padding: 11px;
  z-index: 10;
  bottom: 20px;
  right: 20px;
  width: 200px;
  height: 50px;
  color: #FFFFFF;
  font-weight: bolder;
  font-family: 'Dosis', sans-serif;
  text-align: center;
}
body, html, .content {
  background-color: #2D4452;
  /*height: 100%;*/
  min-height: calc(93.7vh - 200px);
}
footer {
  background-color: #F2EABC;
  color: #103344;
  padding: 20px 30px 10px 30px;
  font-weight: bold;
  height: 200px;
  /*margin-top: 200px;*/
}
footer h2 {
  color: #2F2F2E;
  font-weight: bolder;
  font-size: 25px;
}
footer #sns {
  padding-top: 15px;
  font-size: 15px;
}



